.btn {
  background-color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.btn.pressed {
  background-color: #931d89;
}

.btn .btnTxt {
  color: #931d89;
  font-size: 16px;
  font-weight: bold;
}

.btn.pressed .btnTxt {
  color: #fff;
}
