.saved-screen {
  padding: 20px;
  background-color: #f5f5f5;
}

.list-container {
  overflow-y: auto;
  background-color: transparent;
}

.content {
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card-saved {
  background-color: #ffffff;
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.text {
  font-size: 16px;
  color: #931d89;
  margin-bottom: 10px;
}

.number-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.number-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #931d89;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 14px;
}

.share-button {
  background: none;
  border: none;
  color: #ff1f1e;
  cursor: pointer;
  margin-top: 10px;
}

.share-button .material-icons {
  font-size: 12px;
}

.line {
  border: none;
  height: 1px;
  background-color: #ccc;
  margin: 10px 0;
}
